.cart-btn{
    margin-left: 1rem;
    cursor: pointer;
    border-radius: 1.5rem;
    background-color: transparent;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 2rem;
    padding-right: 2rem;
    transition-property: background-color, border-color, color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    font-size: inherit;
    font-family: inherit;
    border: none;
    border: 1px solid #ccc;
}
.cart-btn:hover{
    --tw-border-opacity: 1;
    border-color: rgba(90, 26, 1, var(--tw-border-opacity));
    --tw-bg-opacity: 1;
    background-color: rgba(90, 26, 1, var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.cart-btn:active{
    --tw-border-opacity: 1;
    border-color: rgba(90, 26, 1, var(--tw-border-opacity));
    --tw-bg-opacity: 1;
    background-color: rgba(90, 26, 1, var(--tw-bg-opacity));
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
}
.cart-item-btn{
    margin: 0.25rem;
    margin-left: 1rem;
    width: 3rem;
    cursor: pointer;
    border-radius: 0.375rem;
    border-width: 1px;
